import config from '@/api/config.js';
import {upload} from '@/api/upload.js'
export default{
	data(){
		return{
			imgUrl1:"",
			imgUrl2:""
		}
	},
	filters:{
		filterImg1(img){
			if(img){
				return config.domain + img;
			}else{
				return '../../../static/images/shenfen2.png'
			}
		},
		filterImg2(img){
			if(img){
				return config.domain + img;
			}else{
				return '../../../static/images/shenfen1.png'
			}
		}
	},
	methods:{
		clickPositive(){
			upload().then((res)=>{
				this.imgUrl1 = res;
				let imgSrc1 = res;
				this.certificate(1,res).then((posRes)=>{
					if(parseInt(posRes.code) === -1){
						uni.showToast({
						    title: '识别错误,请重新上传',
							icon:'none',
						    duration: 4000
						});
					}else{
						this.$emit("clickPos",posRes.data,imgSrc1)
					}
					
				})
			})
		},
		clickReverse(){
			upload().then((res)=>{
				this.imgUrl2 = res;
				let imgSrc2 = res;
				this.certificate(0,res).then((revRes)=>{
					if(parseInt(revRes.code) === -1){
						uni.showToast({
						    title: '识别错误,请重新上传',
							icon:'none',
						    duration: 4000
						});
					}else{
						this.$emit("clickRev",revRes.data,imgSrc2)
					}
					
				})
			})
		},
		async certificate(num,imgRes){
			return await new Promise((resolve,reject)=>{
				uni.request({
				    url: config.domain + 'api.php?s=/member/getIdCardInfo',
					method: 'POST',
				    data: {
				        token: uni.getStorageSync('token'),
						img_url: imgRes,
						data_carcard_type:num,
				    },
				    header: {
				        'content-type': 'application/json'
				    },
				    success(res){
						resolve(res.data)
				    }
				});
			})
		}
	}
}
